/*! 退休审批表-查看 css */
input[type='file'] {
  display: none;
}
.txspb-see {
  padding: 0;
  min-height: 100%;
  .title {
    line-height: @132px;
    padding: 0 @30px;
    .btn {
      width: @160px;
      height: @50px;
      font-size: @28px;
      line-height: @50px;
      font-weight: normal;
    }
    span {
      color: rgb(0, 124, 247);
    }
  }
  ul {
    width: 100%;
    padding: 0 @30px;
  }
  li {
    border: 1px solid #ddd;
    background-color: #fff;
    padding: 0 @30px @30px;
    margin-bottom: @30px;
    .name {
      font-size: @28px;
      color: #333;
      line-height: @80px;
      .btn {
        display: flex;
        width: @170px;
        background-color: transparent;
        height: @44px;
        line-height: 1;
        font-size: @24px;
        color: #999;
        border: 1px solid #ddd;
        &:last-of-type {
          width: @120px;
          margin-left: @20px;
        }
      }
    }
  }
  .icon-reload {
    width: @22px;
    height: @22px;
    background-image: url(../images/icon/icon-reload.png);
    margin-right: @10px;
  }
  .icon-delete {
    width: @18px;
    height: @20px;
    background-image: url(../images/icon/icon-delete.png);
    margin-right: @10px;
  }
  .btn-box{
    padding: @10px @60px @100px;
  }
  img {
    width: 100%;
    height: auto;
  }
}
