/*! 退休审批表 css */
.txspb {
  padding: 0;
  min-height: 100%;
  .content {
    padding: 0 @30px;
  }
  p {
    text-indent: 2em;
    font-size: @28px;
    line-height: @40px;
    color: #333;
    padding-bottom: @10px;
  }
  .img-box {
    padding: @14px @30px;
    img {
      display: block;
      max-width: 100%;
      height: auto;
      margin-bottom: @30px;
    }
  }
}
.scale {
  position: fixed;
  z-index: 9999;
  padding: 0 @30px;
  text-align: center;
  font-size: @34px;
  line-height: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  .index {
    padding: @30px 0;
  }
  span {
    color: #007cf7;
  }
  img {
    display: block;
    width: 100%;
    height: auto;
    margin-bottom: @30px;
  }
}
